import React from 'react'
import { useModal, OpenSinglePackModal } from 'components/Modal'
import Box from 'components/Box'
import Flex from 'components/Flex'
import { PackageFrame, PackageImage, CardText } from './styles'

const CommonPackageCard = ({ data }) => {
  const [onPresentOpenSinglePackModal] = useModal(<OpenSinglePackModal data={data} />)

  return (
    <PackageFrame url="/images/inventory-frame-common.png" onClick={onPresentOpenSinglePackModal}>
      <Flex flexDirection="column" justifyContent="center" pt="25px" pb="25px" height="100%" alignItems="center">
        <PackageImage src="/images/common.png" />
        <Box width="100%" mt="20px" pl="20px" pr="20px" mb="20px" textAlign="center">
          <CardText fontSize="2.2rem" mb="10px">
            Common pack #{data}
          </CardText>
          <CardText fontSize="1.4rem">ETA: few seconds</CardText>
        </Box>
      </Flex>
    </PackageFrame>
  )
}

export default CommonPackageCard
