import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import useRefresh from 'hooks/useRefresh'
import { useCyblockPresaleContract } from '../../../hooks/useContract'
import { filterPackageIndexes } from '../utils/formatPackageCount'

const DEFAULT_DATA = {
  commonPackageData: [],
  rarePackageData: [],
  epicPackageData: [],
}

const useGetInventoryData = () => {
  const { account } = useWeb3React()
  const [data, setData] = useState(DEFAULT_DATA)
  const [isLoading, setIsLoading] = useState(true)
  const { slowRefresh, fastRefresh } = useRefresh()
  const commonPackageContract = useCyblockPresaleContract('0x619B88951A1a010328655688fedD22f71D06030D')
  const rarePackageContract = useCyblockPresaleContract('0x619B88951A1a010328655688fedD22f71D06030D')
  const epicPackageContract = useCyblockPresaleContract('0x619B88951A1a010328655688fedD22f71D06030D')
  const getNumberOfUserPackage = async (packageContract) => {
    try {
      const packageIndexes = await packageContract.methods.userCanOpenPackIndexs(account).call()
      return filterPackageIndexes(packageIndexes)
    } catch (error) {
      console.log(error, 'Cannot fetch package index info')
      return []
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const numberOfCommonPackage = await getNumberOfUserPackage(commonPackageContract)
        const numberOfRarePackage = await getNumberOfUserPackage(rarePackageContract)
        const numberOfEpicPackage = await getNumberOfUserPackage(epicPackageContract)
        setData({
          ...data,
          commonPackageData: numberOfCommonPackage,
        })
        setIsLoading(false)
      } catch (error) {
        setIsLoading(false)
        console.log(error, 'error')
      }
    }
    if (account) {
      fetchData()
    }
  }, [account, fastRefresh])

  return [data, isLoading]
}

export default useGetInventoryData
