import React, { useState } from 'react'
import styled from 'styled-components'
import { useWeb3React } from '@web3-react/core'
import { useModal, OpenPackModal } from 'components/Modal'
import Box from 'components/Box'
import Flex from 'components/Flex'
import Text from 'components/Text'
import PageLoading from 'components/PageLoading'
import { CommonPackageCard, RarePackageCard, EpicPackageCard } from './components/PackageCard'
import { CommonCyblocCard, RareCyblocCard } from './components/CyblocCard'

const Wrapper = styled(Box)`
  background-color: #191123;
  min-height: inherit;
  padding-bottom: 45px;
`
const Row = styled(Box)`
  max-width: 1000px;
  margin: 0 auto;

  @media (max-width: 1100px) {
    max-width: 800px;
  }

  @media (max-width: 600px) {
    max-width: 400px;
  }
`
const StyledHeaderContent = styled(Flex)`
  justify-content: space-between;
  align-items: center;

  @media (max-width: 600px) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 40px;
  }
`

const StyledHeaderText = styled(Text)`
  font-size: 3.2rem;
  line-height: 48px;
  letter-spacing: 2px;
  font-weight: 700;
  color: #f3f3f3;
  padding: 30px 0px;

  @media (max-width: 600px) {
    padding: 0px;
  }
`
const OpenPackButton = styled(Flex)`
  justify-content: center;
  align-items: center;
  background-image: url('/images/open-pack-frame.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  width: 280px;
  transition: background-image 0.3s ease-in, transform 0.1s ease-in;
  padding: 13px 0px;
  border-radius: 2px;

  &:not(:last-of-type) {
    margin-bottom: 20px;
  }

  &:hover {
    cursor: pointer;
  }

  &:active {
    transform: translateY(-2px);
    opacity: 80%;
  }

  & > div {
    font-size: 1.6rem;
    color: #fefefe;
    line-height: 17.15px;
    letter-spacing: 2px;
    font-weight: 700;
  }
`
const StyledCardWrapper = styled(Flex)`
  flex-wrap: wrap;
  justify-content: flex-start;

  @media (max-width: 1100px) {
    justify-content: center;
  }
`

const Inventory = ({ packageData, isLoading }) => {
  const { account } = useWeb3React()
  const [packInfo, setPackInfo] = useState(null)
  const [onPresentOpenPackModal] = useModal(<OpenPackModal packageData={packageData} />)
  const { commonPackageData, rarePackageData, epicPackageData } = packageData

  return (
    <Wrapper>
      <Row>
        {isLoading ? (
          <PageLoading />
        ) : (
          <>
            <StyledHeaderContent>
              <StyledHeaderText>Packs</StyledHeaderText>
              <OpenPackButton onClick={onPresentOpenPackModal}>
                <Text>Open multiple packs</Text>
              </OpenPackButton>
            </StyledHeaderContent>
            <StyledCardWrapper>
              {commonPackageData.map((data) => {
                return (
                  <CommonPackageCard
                    data={data}
                    onClickPackage={() => {
                      setPackInfo(data)
                    }}
                  />
                )
              })}
              {rarePackageData.map((data) => {
                return <RarePackageCard data={data} />
              })}
              {epicPackageData.map((data) => {
                return <EpicPackageCard data={data} />
              })}
            </StyledCardWrapper>
            <StyledHeaderContent>
              <StyledHeaderText>Cyblocs</StyledHeaderText>
            </StyledHeaderContent>
            <Flex flexWrap="wrap" justifyContent="center">
              <CommonCyblocCard />
              <RareCyblocCard />
            </Flex>
          </>
        )}
      </Row>
    </Wrapper>
  )
}

export default Inventory
