import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { unPack } from 'utils/callHelpers'
import { useCyblockPresaleContract } from './useContract'

// We will select correct contract, based on selected package
const useOpenPackage = (packageContractAddress) => {
  const { account } = useWeb3React()
  const cyblockPresaleContract = useCyblockPresaleContract(packageContractAddress)
  const handleUnpack = useCallback(
    async (packages, proof) => {
      const txHash = await unPack(cyblockPresaleContract, account, packages, proof)
      return txHash
    },
    [account, cyblockPresaleContract],
  )

  return { onUnpack: handleUnpack }
}

export default useOpenPackage
