export const buyCyblocPack = async (cyblockPresaleContract, quantity, amount, account) => {
  return cyblockPresaleContract.methods
    .buyCyblocsPacks(quantity, '100', [
      1,
      '0x63616e6469646174653100000000000000000000000000000000000000000000',
      '0x63616e6469646174653100000000000000000000000000000000000000000000',
    ])
    .send({
      from: account,
      value: amount,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const unPack = async (cyblockPresaleContract, account, packages, proofs) => {
  return cyblockPresaleContract.methods
    .unpack(packages, proofs)
    .send({
      from: account,
    })
    .on('transactionHash', (tx) => {
      return tx.transactionHash
    })
}

export const getPackageIndex = async (cyblockPresaleContract, address, startIndex, endIndex) => {
  return cyblockPresaleContract.methods.getMyPackageIndex(address, startIndex, endIndex).call()
}
