import React, { useContext, useState } from 'react'
import { CSSTransition, SwitchTransition } from 'react-transition-group'
import Text from 'components/Text'
import Flex from 'components/Flex'
import Box from 'components/Box'
import LoaderIcon from 'components/LoaderIcon'
import { PurchaseContext } from '../../context/PurchaseContext'
import {
  StatsText,
  StyledChangeValueButton,
  StyledClassName,
  StyledClassPercentage,
  StyledClassInput,
  StyledInput,
  PreviewImage,
  PackageWrapper,
  PackIcon,
  CardWrapper,
  ItemInfo,
  ItemWrapper,
  TextWrapper,
  StyledButton,
  StyledNumberOfPackage,
  StyledClassInfoHeader,
  PreviewText,
} from '../styled'
import theme from '../styled/colors'

const Stats = () => {
  const { epic } = theme

  return (
    <Box>
      <Text color="#F3F3F3" fontSize="1.6rem" pb="20px">
        Chance to obtain each Class (%)
      </Text>
      <Flex pb="20px" alignItems="center">
        <StyledClassName>Bronze</StyledClassName>
        <StyledClassPercentage>98.30</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={98.3} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Silver</StyledClassName>
        <StyledClassPercentage>54.94</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={54.9} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Gold</StyledClassName>
        <StyledClassPercentage>6.53</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={6.53} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Platinum</StyledClassName>
        <StyledClassPercentage>0.42</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={0.42} />
        </StyledClassInput>
      </Flex>
      <Flex>
        <StyledClassName>Legendary</StyledClassName>
        <StyledClassPercentage>0.07</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={epic.inputBackgroundColor} percentage={0.07} />
        </StyledClassInput>
      </Flex>
    </Box>
  )
}

const Preview = () => {
  return (
    <Flex justifyContent="center">
      <PreviewImage src="/images/preview-common.png" />
    </Flex>
  )
}

const PackageCommon = ({ onChangeValue, onHandleBuyPack, payAmount }) => {
  const { state } = useContext(PurchaseContext)
  const [isPreview, setIsPreview] = useState(false)
  const { numberOfPackage, isLoading } = state
  const { epic } = theme

  return (
    <PackageWrapper alignItems="center">
      <ItemWrapper>
        <CardWrapper cardBackground={epic.cardBackground}>
          <PackIcon src="/images/epic.png" />
          <TextWrapper>
            <Flex>
              <StyledChangeValueButton
                color="#F3F3F3"
                fontSize="7rem"
                fontWeight="600"
                onClick={() => onChangeValue('down')}
              >
                -
              </StyledChangeValueButton>
              <StyledNumberOfPackage>{numberOfPackage}</StyledNumberOfPackage>
              <StyledChangeValueButton
                color="#F3F3F3"
                fontSize="7rem"
                fontWeight="600"
                onClick={() => onChangeValue('up')}
              >
                +
              </StyledChangeValueButton>
            </Flex>
          </TextWrapper>
          <Text color="#F3F3F3" fontSize="2rem" fontWeight="600">
            3 BNB (~1000$)
          </Text>
          <StyledButton
            buttonBackgroundColor={epic.buttonBackgroundColor}
            buttonDisabled={epic.buttonDisabled}
            isLoading={isLoading}
            onClick={() => {
              if (!isLoading) {
                onHandleBuyPack()
              }
            }}
          >
            {isLoading ? (
              <LoaderIcon />
            ) : (
              <Text color="#110A19" fontWeight="700">
                Buy for 3 BNB
              </Text>
            )}
          </StyledButton>
        </CardWrapper>
      </ItemWrapper>
      <ItemInfo cardBackgroundColor={epic.cardBackgroundColor}>
        <StyledClassInfoHeader>
          <PreviewText isPreview={isPreview} onClick={() => setIsPreview(true)}>
            Preview
          </PreviewText>
          <StatsText isPreview={isPreview} onClick={() => setIsPreview(false)}>
            Probability
          </StatsText>
        </StyledClassInfoHeader>
        <SwitchTransition mode="out-in">
          <CSSTransition
            key={isPreview}
            addEndListener={(node, done) => {
              node.addEventListener('transitionend', done, false)
            }}
            classNames="fade"
          >
            {isPreview ? <Preview /> : <Stats />}
          </CSSTransition>
        </SwitchTransition>
      </ItemInfo>
    </PackageWrapper>
  )
}

export default PackageCommon
