import { useCallback } from 'react'
import { useWeb3React } from '@web3-react/core'
import { buyCyblocPack } from 'utils/callHelpers'
import { useCyblockPresaleContract } from './useContract'

// We will select correct contract, based on selected package
const useBuyCyblocsPacks = (packageAddress) => {
  const { account } = useWeb3React()
  const cyblockPresaleContract = useCyblockPresaleContract(packageAddress)
  const handleBuyCyblockPack = useCallback(
    async (quantity, amount) => {
      const txHash = await buyCyblocPack(cyblockPresaleContract, quantity, amount, account)
      return txHash
    },
    [account, cyblockPresaleContract],
  )

  return { onBuyPack: handleBuyCyblockPack }
}

export default useBuyCyblocsPacks
